const iconMinus = `
  <svg width="12" height="2" viewBox="0 0 12 2" fill="none" xmlns="http://www.w3.org/2000/svg">
    <rect width="12" height="2" fill="#1E2587"/>
  </svg>
`;

const iconPlus = `
  <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
    <path fill-rule="evenodd" clip-rule="evenodd" d="M7 0H5V5H0V7H5V12H7V7H12V5H7V0Z" fill="#1E2587"/>
  </svg>
`;

const ProductActionsComponent = ({ quantity = 0, minQuantity = 0 }) => {
  return `
    <div class="product__actions">
      <button class="actions__button product__quantity--minus">
        ${iconMinus}
      </button>

      <input
        type="number"
        placeholder="0"
        value="${quantity}"
        min="${minQuantity}"
        class="product__quantity"
      />

      <button class="actions__button product__quantity--add">
        ${iconPlus}
      </button>
    </div>
  `;
};

export default ProductActionsComponent;
